<template>
  <div class="three-level-address" id="three_level_address">
    <div class="region-div">
      <!-- <button class="cho_btn" readonly="readonly" @click="showAddressPicker()">
        <span></span>
        <i class="el-icon-caret-bottom"></i>
      </button>-->
      <mt-popup v-model="regionVisible" position="bottom" class="region-popup">
        <mt-picker
          :slots="myAddressSlots"
          valueKey="name"
          :visibleItemCount="3"
          @change="addressChange"
          :itemHeight="40"
          :showToolbar="true"
        >
          <div class="wrap">
            <button @click="handleConfirm" class="cancel"></button>
            <button @click="handleConfirm" class="ensure"></button>
          </div>
        </mt-picker>
      </mt-popup>
    </div>
  </div>
</template>

<script>
//json
import threeLevelAddress from "../../../static/js/json/threeLevelAddress.json";

// 
export default {
  props: ["dc"],
  data() {
    return {
      region: "", //
      province: "", //
      city: "", //
      county: "", //
      provinceCode: "", //
      cityCode: "", //
      countyCode: "", //
      regionVisible: false, //
      regionInit: false, //picker3

      //picker
      myAddressSlots: [
        //
        {
          flex: 1,
          values: this.getProvinceArr(), //
          className: "slot1",
          textAlign: "center"
        }, //
        {
          divider: true,
          content: "",
          className: "slot2"
        }, //
        {
          flex: 1,
          values: this.getCityArr(""),
          className: "slot3",
          textAlign: "center"
        },
        {
          divider: true,
          content: "",
          className: "slot4"
        }, //
        {
          flex: 1,
          values: this.getCountyArr("", ""),
          className: "slot5",
          textAlign: "center"
        }
      ]
    };
  },

  methods: {
    //
    showAddressPicker() {
      this.regionVisible = true;
    },
    handleConfirm() {
      this.regionVisible = false;
    },
    //pickerchange
    addressChange(picker, values) {
      console.log(picker);
      console.table(values);
      if (this.regionInit) {
        //
        this.region = values[0]["name"] + values[1]["name"] + values[2]["name"];
        this.province = values[0]["name"];
        this.city = values[1]["name"];
        this.county = values[2]["name"];
        this.provinceCode = values[0]["code"];
        this.cityCode = values[1]["code"];
        this.countyCode = values[2]["code"];
        console.log(picker.getSlotValue(0));
        console.table(picker.getSlotValues(0));
        console.table(picker.getValues()); //
        picker.setSlotValues(1, this.getCityArr(values[0]["name"]));
        picker.setSlotValues(
          2,
          this.getCountyArr(values[0]["name"], values[1]["name"])
        );
      } else {
        this.regionInit = true;
      }
    }, //json
    getProvinceArr() {
      let provinceArr = [];
      threeLevelAddress.forEach(function(item) {
        let obj = {};
        obj.name = item.name;
        obj.code = item.code;
        provinceArr.push(obj);
      });
      return provinceArr;
    }, //json
    getCityArr(province) {
      // console.log("" + province);
      let cityArr = [];
      threeLevelAddress.forEach(function(item) {
        if (item.name === province) {
          item.children.forEach(function(args) {
            let obj = {};
            obj.name = args.name;
            obj.code = args.code;
            cityArr.push(obj);
          });
        }
      });
      return cityArr;
    }, //json
    getCountyArr(province, city) {
      let countyArr = [];
      threeLevelAddress.forEach(function(item) {
        if (item.name === province) {
          item.children.forEach(function(args) {
            if (args.name === city) {
              args.children.forEach(function(param) {
                let obj = {};
                obj.name = param.name;
                obj.code = param.code;
                countyArr.push(obj);
              });
            }
          });
        }
      });
      // console.log(countyArr);
      return countyArr;
    }
  },
  
};
</script>
<style scoped>
/*  */
.wrap {
  width: 100%;
  display: flex;
  justify-content: space-around;
  margin-top: 0.3rem;
}
.cancel {
  width: 0.5rem;
  height: 0.3rem;
  border: 0;
  background: #28a289;
  color: #fff;
  line-height: 0.2rem;
  outline: none;
}
.ensure {
  width: 0.5rem;
  height: 0.3rem;
  border: 0;
  background: #28a289;
  color: #fff;
  line-height: 0.2rem;
  outline: none;
}
/*  */
.cho_btn {
  width: 0.8rem;
  height: 0.24rem;
  background: #ffb84c;
  border: 0;
  display: flex;
  justify-content: space-around;
  border-radius: 0.03rem;
  outline: none;
  position: absolute;
  right: 0.1rem;
  top: 0.42rem;
}
.cho_btn span,
i {
  font-size: 0.12rem;
  color: #fff;
  line-height: 0.24rem;
}
.three-level-address {
  width: 100%;
  text-align: left;
  color: #ffffff;
}
.region-div {
  width: 100%;
}
.input-icon {
  display: inline-block;
  vertical-align: middle;
}
.input-icon i {
  font-size: 2rem;
}
.region-popup {
  width: 100%;
}
.data-show-div {
  margin-left: 1rem;
  color: #45c473;
}
.data-show-div span {
  color: #ffffff;
  font-size: 0.8rem;
}
</style>




