<template>
  <div class="test_topic">
    <div class="title_topic">
      <h3></h3>
    </div>
    <div class="recommend">
      <p></p>
      <p>!</p>
    </div>
    <div class="topic_box">
      <h4> 1 </h4>
      <p></p>
      <div class="answer">
        <div class="yes">
          <span></span>
        </div>
        <div class="no">
          <span></span>
        </div>
      </div>
    </div>
    <div class="left_but">
      <canvas id="firCan" width="1000" height="500">, canvas, .</canvas>
    </div>
    <div class="right_but">
      <canvas id="sonCan" width="1000" height="500">, canvas, .</canvas>
    </div>
    <div class="submit">
      <span></span>
    </div>
    <div :class="isShow ? 'pop_up' : 'pou_upA'">
        <p></p>
        <p></p>
        <p>, </p>
        <div class="res">
            <div class="wait" @click="go_out()">
                <span></span>
            </div>
            <div class="now" @click="begin()">
                <span></span>
            </div>
        </div>
    </div>
    <div :class="isMask ? 'mask' : 'maskA'"></div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      isMask: true,
      isShow: true
    }
  },
  methods: {
    go_out() {
      this.$router.push({name: 'exportsTest'});
    },
    begin() {
      this.isMask = false;
      this.isShow = false;
    }
  },
  mounted() {
    // 1,canvas
    var canvas = document.getElementById("firCan");
    // 2,2D
    var context = canvas.getContext("2d");
    // 3, 
    context.beginPath();
    context.arc(-120, 130, 150, 0, Math.PI * 2, false);
    context.closePath();
    context.fillStyle = "#FE4266";
    context.fill();

    // 1,canvas
    var canvas = document.getElementById("sonCan");
    // 2,2D
    var context = canvas.getContext("2d");
    // 3, 
    context.beginPath();
    context.arc(170, 130, 150, 0, Math.PI * 2, false);
    context.closePath();
    context.fillStyle = "#FE4266";
    context.fill();
  }
};
</script>
<style scoped>
/*  */
.pop_up {
  width: 2.53rem;
  height: 2.15rem;
  background: #fff;
  border-radius: 0.05rem;
  position: absolute;
  top: 1.66rem;
  left: 0.6rem;
  z-index: 1;
}
.pop_upA {
  display: none;
}
.pop_up p {
  width: 100%;
  text-align: center;
  line-height: 0.2rem;
}
.pop_up p:first-child {
  padding-top: 0.3rem;
}
.pop_up .res {
  margin-top: 0.55rem;
  width: 100%;
  display: flex;
  justify-content: space-around;
}
.pop_up .res .wait {
  width: 0.75rem;
  height: 0.4rem;
  border: 0.015rem solid #FFBC3C;
  text-align: center;
  line-height: 0.4rem;
  color: #3A3A39;
  border-radius: 0.05rem;
}
.pop_up .res .now {
  width: 0.75rem;
  height: 0.4rem;
  background: #FFBC3C;
  text-align: center;
  line-height: 0.4rem;
  color: #fff;
  border-radius: 0.05rem;
}
/*  */
.mask {
    width: 100%;
    background: #000;
    opacity: 0.6;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
}
.maskA {
  display: none;
}
/* div */
.test_topic {
  position: relative;
  overflow: hidden;
}
/*  */
.left_but {
  width: 0.49rem;
  height: 2.56rem;
  border-radius: 0.03rem;
  background: #fff;
  position: absolute;
  left: 0;
  top: 2rem;
}
.right_but {
  width: 0.49rem;
  height: 2.56rem;
  border-radius: 0.03rem;
  background: #fff;
  position: absolute;
  right: 0;
  top: 2rem;
}
#firCan {
  position: absolute;
  left: 0;
  top: 0;
}
#sonCan {
  position: absolute;
  left: 0;
  top: 0;
}
/*  */
.submit {
  width: 2.58rem;
  height: 0.5rem;
  background: #28a289;
  text-align: center;
  line-height: 0.5rem;
  border-radius: 0.05rem;
  margin-top: 0.66rem;
  margin-left: 0.6rem;
  margin-bottom: 0.23rem;
  color: #fff;
}
/*  */
.answer {
  width: 100%;
  margin-top: 0.61rem;
  display: flex;
  justify-content: space-between;
}
.answer .yes {
  width: 0.75rem;
  height: 0.4rem;
  border: 0.02rem solid #ffbc3c;
  border-radius: 0.03rem;
  text-align: center;
  line-height: 0.4rem;
  margin-left: 0.2rem;
  color: #616161;
}
.answer .no {
  width: 0.75rem;
  height: 0.4rem;
  border: 0.02rem solid #ffbc3c;
  border-radius: 0.03rem;
  text-align: center;
  line-height: 0.4rem;
  margin-right: 0.2rem;
  color: #616161;
}
.topic_box {
  width: 2.5rem;
  height: 2.7rem;
  background: #fff;
  border-radius: 0.03rem;
  margin-left: 0.63rem;
  margin-top: 0.37rem;
}
.topic_box h4 {
  font-size: 0.17rem;
  color: #616161;
  padding-top: 0.31rem;
  margin-bottom: 0.28rem;
  margin-left: 0.04rem;
}
.topic_box p {
  color: #616161;
  font-size: 0.13rem;
  line-height: 0.18rem;
  width: 2.2rem;
  height: 0.35rem;
  margin-left: 0.16rem;
}
/*  */
.recommend {
  width: 100%;
  margin-top: 0.3rem;
}
.recommend p {
  text-align: center;
  line-height: 0.2rem;
}
/*  */
.title_topic {
  width: 100%;
  height: 0.4rem;
  background: #28a289;
}
.title_topic h3 {
  font-size: 0.18rem;
  color: #fff;
  text-align: center;
  line-height: 0.4rem;
}
</style>



