<script>
import levelTwo from "../common/levelTwo";
export default {
  data() {
    return {
      isMask: false,
      isShow: false,
      isComplex: false,
      isNature: false,
      isType: false,
      isMajor: false,
      isMore: false,
      isInt1: false,
      isInt2: false,
      isInt3: false,
      isInt4: false,
      num: 0
    };
  },
  methods: {
    complex_show() {
      this.isComplex = true;
      if (this.isComplex = true) {
          this.isNature = false;
          this.isType = false;
          this.isMajor = false,
          this.isMore = false;
      }
    },
    choose_city() {
      this.$refs.two.showAddressPicker();
    },
    choose_nature() {
      this.isNature = !this.isNature;
    },
    choose_type() {
      this.isType = !this.isType;
    },
    choose_major() {
      this.isMajor = !this.isMajor;
    },
    add_input(e) {
      console.log(e);
      this.num += 1;
      console.log(this.num);
      if (this.num == 1) {
        this.isInt1 = true;
      } else if (this.num == 2) {
        this.isInt2 = true;
      } else if (this.num == 3) {
        this.isInt3 = true;
      } else if (this.num == 4) {
        this.isInt4 = true;
      }
    }
  },
  components: {
    levelTwo
  }
};
</script>
