<template>
  <div class="domain">
    <div class="domain_search">
      <input type="text">
      <i class="el-icon-search search"></i>
    </div>
    <div class="domain_kind">
      <p @click="major_list()">
        
        <!-- <i v-if="isRotate" class="iconfont unrotate">&#xe600;</i> -->
        <i class="iconfont rotate">&#xe600;</i>
      </p>
      <p @click="level_list()">
        
        <!-- <i v-if="isRot" class="iconfont unrotate">&#xe600;</i> -->
        <i class="iconfont rotate">&#xe600;</i>
      </p>
    </div>
    <div :class="isShow || isDisplay ? 'mask' : 'maskA'"></div>
    <div :class="isShow ? 'domain_sort' : 'domain_sortA'">
      <ul>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
      </ul>
    </div>
    <div :class="isDisplay ? 'domain_level' : 'domain_levelA'">
      <ul>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
      </ul>
    </div>
    <div class="career">
      <ul>
        <router-link to="/query/specialty">
          <li>
            <p>
              <i class="iconfont">&#xe60c;</i>
              : 
            </p>
            <p>
              <span></span>
              <span></span>
            </p>
          </li>
        </router-link>
        <li>
          <p>
            <i class="iconfont">&#xe60c;</i>
            : 
          </p>
          <p>
            <span></span>
            <span></span>
          </p>
        </li>
        <li>
          <p>
            <i class="iconfont">&#xe60c;</i>
            : 
          </p>
          <p>
            <span></span>
            <span></span>
          </p>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
// 
export default {
  data() {
    return {
      isShow: false,
      isDisplay: false,
    };
  },
  methods: {
    major_list() {
      this.isShow = true;
    },
    level_list() {
      this.isDisplay = true;
    }
  },
};
</script>
<style scoped>
/*  */
.mask {
  width: 100%;
  height: 5.22rem;
  background: #000;
  opacity: 0.7;
  position: absolute;
}
.maskA {
  width: 100%;
  height: 5.22rem;
  background: #000;
  opacity: 0.7;
  position: absolute;
  display: none;
}
/*  */
.domain_levelA {
  width: 0.8rem;
  overflow: hidden;
  border-radius: 0.05rem;
  background: #fff;
  position: absolute;
  left: 2.07rem;
  top: 1.3rem;
  display: none;
}
.domain_level {
  width: 0.8rem;
  overflow: hidden;
  border-radius: 0.05rem;
  background: #fff;
  position: absolute;
  left: 2.07rem;
  top: 1.3rem;
}
.domain_level li {
  width: 100%;
  height: 0.23rem;
  color: #333;
  font-size: 0.14rem;
  line-height: 0.23rem;
  text-indent: 0.07rem;
  border-bottom: 0.01rem solid #dedcdc;
}
/*  */
.domain_sortA {
  width: 0.8rem;
  overflow: hidden;
  border-radius: 0.05rem;
  background: #fff;
  position: absolute;
  left: 0.85rem;
  top: 1.3rem;
  display: none;
}
.domain_sort {
  width: 0.8rem;
  overflow: hidden;
  border-radius: 0.05rem;
  background: #fff;
  position: absolute;
  left: 0.85rem;
  top: 1.3rem;
}
.domain_sort li {
  width: 100%;
  height: 0.23rem;
  color: #333;
  font-size: 0.14rem;
  line-height: 0.23rem;
  text-indent: 0.07rem;
  border-bottom: 0.01rem solid #dedcdc;
}
/*  */
.career {
  width: 100%;
  margin-top: 0.11rem;
  display: flex;
  justify-content: center;
}
.career li {
  width: 3.65rem;
  height: 0.7rem;
  background: #fff;
  margin-bottom: 0.07rem;
  border-radius: 0.05rem;
}
.career li p:first-child {
  margin-left: 0.33rem;
  padding-top: 0.12rem;
  color: #333;
}
.career li p:first-child i {
  margin-left: -0.2rem;
  color: #ffbc3c;
}
.career li p:last-child {
  margin-left: 0.33rem;
  margin-top: 0.1rem;
}
.career li p:last-child span {
  display: inline-block;
  width: 0.3rem;
  text-align: center;
  background: #28a289;
  line-height: 0.15rem;
  color: #fff;
  border-radius: 0.03rem;
  font-size: 0.12rem;
}
.career li p:last-child span:last-child {
  margin-left: 0.19rem;
}
/*  */
.domain_kind {
  width: 100%;
  height: 0.34rem;
  line-height: 0.34rem;
  margin-top: 0.04rem;
  background: #fff;
  display: flex;
}
/* .domain_kind p .rotate {
  font-size: 0.12rem;
  color: lightgray;
} */
.domain_kind p .rotate {
  display: inline-block;
  font-size: 0.12rem;
  color: #FFB84C;
  transform:rotate(90deg);
}
.domain_kind p:first-child {
  margin-left: 0.9rem;
  margin-right: 0.49rem;
}
/*  */
.domain_search {
  position: relative;
  display: flex;
  justify-content: center;
}
.domain_search input {
  width: 3.45rem;
  height: 0.4rem;
  background: #f3f3f3;
  border-radius: 1rem;
  border: 0.01rem solid #b2b2b2;
  padding-left: 0.35rem;
  box-sizing: border-box;
}
.search {
  line-height: 0.4rem;
  color: #999999;
  position: absolute;
  left: 0.3rem;
}
</style>




